  .header{
    font-size: 25px;
  }

  .nav-link {
    margin-left: 20px;
  }
  
  #navbar {
    position: fixed;
    width: 100vw;
    background-color: rgb(7, 26, 47, 0) !important;
  }
  
  #nav-btn {
    text-decoration: inherit !important;
    color: #d7d7d7;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  
  #nav-btn:hover {
    color: #f0f2f5;
    text-shadow: 0px 0px 0.45px #ffffff;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  
  #nav-disable {
    cursor: default !important;
  }
  