video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
  }
  
  #github {
    margin-top: -7px;
  }
  
  .projects-btn {
    margin-right: 50px;
  }
  
  .hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.7);
  }
  
  .main > h1 {
    color: #f0f2f5;
    font-size: 100px;
    margin-top: -100px;
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: 0.05em;
    height: 125px;
  }
  
  .hero-container > p {
    margin-top: 8px;
    color: #f0f2f5;
    font-size: 32px;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  .hero-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  #github-button {
    margin-left: 25px;
  }
  
  @media screen and (max-width: 1084px) {
    .main > h1 {
      font-size: 80px;
      margin-top: -150px;
      height: 100px;
    }
  }
  
  @media screen and (max-width: 960px) {
    .main > h1 {
      font-size: 60px;
      margin-top: -150px;
      height: 80px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .main > h1 {
      font-size: 33px;
      margin-top: -150px;
      height: 41px;
    }
  
    .hero-container > p {
      font-size: 30px;
      margin-top: -90px;
    }
  }
  